@import "./src/styles/index";

.headline {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.headlineHero {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.button {
    font-size: 0.55rem;
    font-weight: 600;
    letter-spacing: 0.05em;
    border-radius: 20px;

    padding: 5px;
    text-transform: uppercase;
}

.buttonUrgent {
    font-size: 0.55rem;
    font-weight: 600;
    letter-spacing: 0.05em;
    border-radius: 20px;
    background-color: $error;
    padding: 5px;
    text-transform: uppercase;
}

.imgWrapper {
    border-radius: 5%;
    width: 100%;
    object-fit: cover;

}

.img {
    border-radius: 5%;
    object-fit: cover;
    width: 100%;
    height: 230px;
    object-position: 30% 70%;
}

.h4 {
    font-size: 22px;
    margin: 10px 0 5px 0;
}

.postedBy {
    margin: 0;
    font-weight: 500;
}

.a {
    text-decoration: none;
    padding-bottom: -2px;
    border-bottom: solid $primary-color 1.5px;
}

.a:hover {
    border-bottom: none;
}

.desc {
    color: $secondary-color-grey;
    font-weight: 300;
}

.aDesc {
    font-weight: 500;
}


.arrow {
    border: solid $primary-color;
    border-radius: 0.5px;
    border-width: 0 1.5px 1.5px 0;
    display: inline-block; 
    padding: 3px;
    margin-left: 5px;
}
  
.right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}

.icon {
    transition: all 0.5s ease-out;
    display: inline-block;
}

.discover:hover .icon {
    transform: translateX(10px);
}

.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.buttonApply {
    padding: 3px 10px;
}

.buttonApply:hover {
    padding: 3px 10px;
    color: white;
}

@media (max-width: 635px) {
    .img {
        height: 300px;
    }
}
