@import "./src/styles/index";

.formDiv {
    width: 50%;
    margin-left: $margin;
    margin-right: 70px;
}

.formOpportunity {
    width: 90%;
}

.error {
    color: $error;
}

.p {
    display: block;
    margin-top: 4px;
    margin-bottom: 7px;
    color: $secondary-color-grey;
}

.pDivider {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.formWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.error {
    border-radius: 7px;
    padding: 15px 30px;
    border: solid $error-border 1px;
    color: $error-border;
    margin-top: 5px;
    margin-bottom: 10px;
}


.formDiv h3 {
    margin-bottom: 30px;
}

.formDiv input,
.formDiv textarea,
.divider input {
    resize: vertical;
    font-family: $font;
    font-size: $primary-font-size;
    padding: 20px;
    border-radius: 5px;
    border: solid $secondary-color-grey 1px;
    margin-bottom: 10px;
}

.formDiv select {
    color: $secondary-color-grey;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

[type="date"]::-webkit-inner-spin-button {
    display: none;
}

[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
}

input[type=date]::-webkit-datetime-edit {
    color: $secondary-color-grey;
}

.formDiv select:focus {
    color: $primary-color;
    border: solid $primary-color 1px;
}

.formDiv input:focus,
.formDiv textarea:focus{
    border: solid $primary-color 1px;
}

.formDiv select:invalid {
    border: solid $error-border 1px;
}

.formDiv input:optional {
    border: solid $secondary-color-grey 1px;
}

.formDiv input:optional:focus {
    border: solid $primary-color 1px;
}

.formDiv input:optional:invalid {
    border: solid $error-border 1px;
}

.formDiv input:invalid,
.formDiv textarea:invalid {
    border: solid $error-border 1px;
}

.formDiv input:out-of-range {
    border: solid $error-border 1px;
}

.formDiv button {
    margin-top: 15px;
    padding: 15px 30px;
}

.formDiv button:hover {
    color: white;
}

.divider input:invalid {
    border: solid $error-border 1px;
}

.divider {
    width: 100%;
    display: flex;
    align-items: stretch;
}


.divider input {
    width: 100%;
    margin-right: 7px;
    //fixes overflowing input box in mozilla
    min-width: 0;
}


.divider [type="number"],
.divider .houseNumber {
    width: 30%;
}

.divider .inputText {
    width: 100%;
}

.divider input:last-child {
    margin-right: 0px;
}

.imgContainer {
    align-self: baseline;
}

.formDiv .category {
    width: 60%;
}

.divider .numberOfParticipants {
    width: 40%;
}

@media (max-width: 1150px) {
    .page {
        display: block;
    }

    .imgContainer {
        display: none;
    }
    .formWrapper {
        width: 100%;
    }

    .formDiv {
        box-sizing: border-box;
        width: 85%;
        margin-left: $margin-mobile;
        margin-right: 0;
    }
    
}