@import "./src/styles/index";

.profileWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.a {
    font-weight: 600;
    text-decoration: none;
    color: $primary-color;
    margin-left: 30px;
    border-bottom: solid $primary-color 1.5px;
}

.a:hover {
    border-bottom: 0;
}

.profile label {
    display: block;
    margin: 10px 10px 30px 0;
    font-weight: 800;
}

.profile input,
.profile textarea {
    width: 100%;
    display: block;
    margin-top: 10px;
    resize: vertical;
    color: $secondary-color-grey;
    font-family: $font;
    font-size: $primary-font-size;
    padding: 20px;
    border-radius: 5px;
    border: solid $secondary-color-grey 1px;
    margin-bottom: 10px;
}

.profile input::placeholder,
.profile textarea::placeholder {
    color: $secondary-color-grey;
  }

.profile textarea {
    width: 70%;
}

.profile {
    width: 100%;
    // display: flex;
    // align-items: stretch;
}


.profile input,
.profile textarea {
    width: 100%;
    margin-right: 7px;
    //fixes overflowing input box in mozilla
    min-width: 0;
}


.profile .inputText {
    width: 100%;
}

.profile input:last-child {
    margin-right: 0px;
}

input:disabled,
textarea:disabled {
    resize: none;
    cursor: text;
    font-family: $font;
    color: $primary-color;
    font-size: $primary-font-size;
    line-height: $line-height;
    background-color: transparent;
    border: none;   
    margin: 0;
    padding: 10px 10px 10px 0;
}

input:focus,
textarea:focus {
    color: $primary-color;
    border: solid $primary-color 1px;
}

input:invalid,
textarea:invalid {
    color: $primary-color;
    border: solid $error-border 1px;
}

@media (max-width: 800px) {

    .profileWrapper {
        display: block;
    }
    
}