@import "./src/styles/index";

.wrapper {
    // display: flex;
    // flex-direction: column;
    // align-items: center;
    margin: 0 auto;
    width: 60%;
}

.infoWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0;
}

.arrow {
    border: solid $primary-color;
    border-radius: 0.5px;
    border-width: 0 1.5px 1.5px 0;
    display: inline-block; 
    padding: 3px;
    margin-left: 5px;
}

.back {
    margin-top: 20px;
}

  
.right {
    margin-right: 7px;
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
}

.icon {
    transition: all 0.5s ease-out;
    display: inline-block;
}

.button {
    padding: 9px 25px;
    background-color: transparent;
    color: $primary-color;
}

.button:hover {
    background-color: $primary-color;
    color: white;
}

.postedBy {
    margin-top: 7px;
    margin-bottom: 0;
}

.imgContainer {
    width: 100%;
    height: 400px;
    margin-bottom: 20px;
}


.container {
    display: flex;
    justify-content: space-between;
}

.aLink {
    text-decoration: none;
    padding-bottom: -2px;
    border-bottom: solid $primary-color 1.5px;
}

.h3 {
    margin-bottom: 0px;
}

.aLink:hover {
    border-bottom: none;
}

@media (max-width: 700px) {
    .wrapper {
        width: auto;
        margin: 0 $margin-mobile 0 $margin-mobile;
    }

    .imgContainer {
        height: 300px;
        margin-bottom: 20px;
    }
  }